@import "//cdn.jsdelivr.net/npm/font-kopub@1.0/kopubbatang.min.css";

.App {
  text-align: center;
  font-family: 'Nanum Gothic';
  display: flex;
  gap: 20px;
  /* flex-wrap: wrap; */
 
 
  /* justify-content: center; */
}

.modal {
  
  background: #fff;
  background: linear-gradient();
  border-radius: 15px;
  padding: 20px;
  max-width: 800px;
  width: 600px;
  /* margin: 20px auto; */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);

}


.modal a.title {
  color: #000;
  
  text-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
  font-size: 20px;
  text-decoration: none;

  /* height: 10px; */
  overflow: hidden;
  margin-bottom: 20px;
  text-overflow: ellipsis;

}

.modal .img {

  font-size: 18px;
  font-weight: bold;
  width: 380px;
  height: 300px;
}

.modal .img img {
  width: 380px;
  height: 300px;
  margin-top: 20px;

}

.modal .button {
  color: #fff;
  border: none;
  padding: 15px 32px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  margin: 20px 0;
  cursor: pointer;
  background-color: #000;
  border-radius: 50px;
}

.modal .button:hover {
  background-color: #2b2a2a;
}

.modal-backdrop {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
}

h2 {
  font-size: 20px;
  font-weight: normal;
}

.modal-backdrop p {

  font-size: 20px;
  width: 300px;
  line-height: 1.6;
  overflow: hidden;
}



@media (max--width:760px) {
  .modal {
    max-width: 90%;
  }
}